import { useHistory } from "react-router-dom";
import styled from 'styled-components';

import { COLOR_GREEN, COLOR_GREEN_TEXT, COLOR_GRAY } from '../constants/styles';
import { KEYWORDS_RANDOM } from '../constants/search'
import Footer from './Footer';
import Header from './Header';
import SearchBar from './SearchBar';
import SearchAdvanced from './SearchAdvanced';

const Wrapper = styled.section`
  padding: 88px 16px 48px;
  @media (min-width: 768px) {
    padding: 0;
  }
`;

const  FirstScreenWrapper = styled.div`
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 88px);
  @media (min-width: 768px) {
    min-height: 100vh;
  }
`;

const Title = styled.h1`
  margin: 0;
  color: ${COLOR_GREEN_TEXT};
  text-align: center;
  font-weight: 200;
  font-size: 32px;
  line-height: 40px;
  @media (min-width: 768px) {
    font-size: 48px;
    line-height: 44px;
  }
`;

const Text = styled.p`
  max-width: 512px;
  margin: 12px 0 0;
  color: ${props => props.green ? COLOR_GREEN_TEXT : COLOR_GRAY};
  text-align: center;
  font-size: 16px;
  font-weight: normal;
  line-height: 22px;
  letter-spacing: 0.005em;
  
  @media (min-width: 768px) {
    margin: ${props => props.green ? '12px' : '24px' } 0 0;
  }
`;

const Button = styled.button`
  margin: 24px 0 0;
  padding: 12px 16px;
  color: ${COLOR_GREEN_TEXT};
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.005em;
  background-color: transparent;
  border: 1px solid ${COLOR_GREEN};
  border-radius: 2px;
  @media (min-width: 768px) {
    margin: 240px 0 0;
  }
  @media (min-width: 1024px) {
    margin: 200px 0 0;
  }
`;

function Home() {
  let history = useHistory()
  
  function handleClickRandomKeywords() {
    const randomIndex = Math.floor(Math.random() * Math.floor(KEYWORDS_RANDOM.length))
    history.push(`/search?q=${KEYWORDS_RANDOM[randomIndex]}`)
  }

  function handleSearch(keyword) {
    history.push(`/search?q=${keyword}`)
  }

  return (
    <Wrapper>
      <FirstScreenWrapper>
        <Header />
        <Title>COVID-19 Chinese Archive</Title>
        <Text>
          This database aims to collect and archive Chinese-langauge online contents on COVID-19, including those that have been deleted.
        </Text>
        <SearchBar search={handleSearch}/>
        <Text green>Enter "*" for searching all articles</Text>
        <SearchAdvanced />
        <Button onClick={handleClickRandomKeywords}>Random Keywords</Button>
      </FirstScreenWrapper>
      <Footer />
    </Wrapper>
  )
}

export default Home;